import Vue from 'vue'
import Router from 'vue-router'
import HomePage from '@/components/HomePage'
import Reserve from '@/components/Reserve'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'HomePage',
      component: HomePage,
      children: [
        {
          path: 'reserve',
          component: Reserve
        }
      ]
    }
  ]
})



